<template>
  <section id="testimony" class="px-4 sm:px-6 md:px-12 my-8 md:my-12">
    <h2 class="text-2xl sm:text-3xl mb-6 font-semibold text-center">Testimonials</h2>

    <div class="relative flex flex-col md:flex-row items-center mb-8">
      <button 
        @click="prevTestimonial" 
        :class="{
          'absolute left-0 top-1/2 transform -translate-y-1/2 p-1 rounded-full transition z-10': true,
          'bg-blue-600 text-white hover:bg-blue-700': currentIndex > 0,
          'bg-gray-400 text-gray-200 cursor-not-allowed': currentIndex === 0
        }"
        :disabled="currentIndex === 0" 
        aria-label="Previous Testimonial"
      >
        <svg class="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 19l-7-7 7-7" />
        </svg>
      </button>

      <div class="flex-1 text-center">
        <transition name="fade" mode="out-in">
          <div :key="currentIndex" class="bg-white p-6 rounded-lg shadow-lg transition-transform transform hover:scale-105">
            <img 
              :src="currentTestimonial.image" 
              :alt="currentTestimonial.name" 
              class="w-20 h-20 sm:w-32 sm:h-32 rounded-full mx-auto mb-4" 
            />
            <p class="font-semibold text-lg">{{ currentTestimonial.name }}</p>
            <a 
              :href="currentTestimonial.link" 
              target="_blank" 
              rel="noopener noreferrer"
              class="text-sm underline sm:text-base text-blue-600 hover:text-blue-800 transition-colors"
            >
              {{ currentTestimonial.position }}
            </a>
            <p class="text-base sm:text-lg md:text-xl mt-4">
              "{{ currentTestimonial.quote }}"
            </p>
          </div>
        </transition>
      </div>

      <button 
        @click="nextTestimonial" 
        :class="{
          'absolute right-0 top-1/2 transform -translate-y-1/2 p-1 rounded-full transition': true,
          'bg-blue-600 text-white hover:bg-blue-700': currentIndex < testimonials.length - 1,
          'bg-gray-400 text-gray-200 cursor-not-allowed': currentIndex === testimonials.length - 1
        }"
        :disabled="currentIndex === testimonials.length - 1" 
        aria-label="Next Testimonial"
      >
        <svg class="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 5l7 7-7 7" />
        </svg>
      </button>
    </div>
  </section>
</template>

<script lang="ts">
import { defineComponent } from 'vue';

interface Testimonial {
  name: string;
  image: string;
  position: string;
  link: string;
  quote: string;
}

export default defineComponent({
  name: 'Testimony',
  data() {
    return {
      currentIndex: 0,
      autoScrollInterval: null as number | null,
      testimonials: [
        {
          name: 'Damai Taribuka',
          image: require('@/assets/images/Damai.jpeg'),
          position: 'Product Owner at ABN AMRO Bank N.V. The Netherlands',
          link: 'https://www.linkedin.com/in/damaitaribuka/',
          quote: 'I have worked with Naresh on a large-scale migration project and I am very positive about our collaboration. Naresh is a team player with a proactive approach and always tries to help and coach others where he can. He is easy to connect with and made a great addition to our team.'
        },
        {
          name: 'Kaviyarasan Ramalingam',
          image: require('@/assets/images/Kavi.jpeg'),
          position: 'Engineering Lead at ING - Process Automation Platform',
          link: 'https://www.linkedin.com/in/kaviyarasan/',
          quote: 'I had the pleasure of working with Naresh. During that time, I was consistently impressed by his professionalism, expertise, and dedication. Naresh has an exceptional ability to work on any new technology. He demonstrated this by developing a new framework for one of our use cases. What truly sets Naresh apart is his positive attitude, willingness to help others, and commitment to continuous learning.'
        }
      ] as Testimonial[]
    };
  },
  computed: {
    currentTestimonial(): Testimonial {
      return this.testimonials[this.currentIndex];
    }
  },
  methods: {
    nextTestimonial() {
      if (this.currentIndex < this.testimonials.length - 1) {
        this.currentIndex++;
      } else {
        this.currentIndex = 0; // Loop back to the first testimonial
      }
    },
    prevTestimonial() {
      if (this.currentIndex > 0) {
        this.currentIndex--;
      } else {
        this.currentIndex = this.testimonials.length - 1; // Loop back to the last testimonial
      }
    },
    startAutoScroll() {
      this.autoScrollInterval = setInterval(() => {
        this.nextTestimonial();
      }, 10000); // Change every 5 seconds
    },
    stopAutoScroll() {
      clearInterval(this.autoScrollInterval as number);
    }
  },
  mounted() {
    this.startAutoScroll();
  },
  beforeUnmount() { // Change from beforeDestroy to beforeUnmount for Vue 3
    this.stopAutoScroll();
  }
});
</script>

<style scoped>
.fade-enter-active, .fade-leave-active {
  transition: opacity 1s ease; /* Duration of the fade effect */
}
.fade-enter, .fade-leave-to {
  opacity: 0; /* Start and end with opacity 0 */
}
.fade-enter-to, .fade-leave {
  opacity: 1; /* Fully visible when entering and leaving */
}
</style>
