<template>
  <section id="contact" class="px-4 md:px-12 my-12">
    <h2 class="text-2xl md:text-3xl mb-6 font-semibold text-center md:text-left">Contact</h2>
    <div class="flex flex-col md:flex-row gap-6 md:gap-12">
      <div class="flex-1">
        <p class="text-center md:text-left">
          I'm always excited to work on new projects and collaborations. Whether you have a question, a proposal, or
          just want to say hello, feel free to get in touch with me using the form below. I look forward to hearing from
          you!
        </p>
      </div>
      <div class="flex-1">
        <form @submit.prevent="sendMessage" class="flex flex-col space-y-4">
          <input 
            type="text" 
            id="name" 
            v-model="name" 
            placeholder="Name" 
            class="p-2 border-2 border-gray-300 rounded-md focus:outline-none focus:border-blue-500 transition"
            :class="{ 'border-red-500': nameError }" 
            @blur="validateName" 
          />
          <input 
            type="email" 
            id="email" 
            v-model="email" 
            placeholder="Email" 
            class="p-2 border-2 border-gray-300 rounded-md focus:outline-none focus:border-blue-500 transition"
            :class="{ 'border-red-500': emailError }" 
            @blur="validateEmail" 
          />
          <textarea 
            id="message" 
            v-model="message" 
            placeholder="Message" 
            rows="4" 
            class="p-2 border-2 border-gray-300 rounded-md focus:outline-none focus:border-blue-500 transition"
            :class="{ 'border-red-500': messageError }" 
            @blur="validateMessage" 
          ></textarea>
          <button 
            type="submit" 
            class="bg-blue-500 text-white py-2 px-4 rounded-md hover:bg-blue-600 transition-colors disabled:opacity-50"
            :disabled="isLoading || nameError || emailError || messageError"
          >
            Send Message
          </button>
        </form>
      </div>
    </div>
    <Notification :message="notificationMessage" :type="notificationType" />
    <Loader :visible="isLoading" />
  </section>
</template>

<script lang="ts">
import { defineComponent, ref } from 'vue';
import emailjs from 'emailjs-com';
import Notification from './Notification.vue';
import Loader from './Loader.vue';

export default defineComponent({
  name: 'Contact',
  components: {
    Notification,
    Loader
  },
  setup() {
    const name = ref<string>('');
    const email = ref<string>('');
    const message = ref<string>('');
    const notificationMessage = ref<string>('');
    const notificationType = ref<'success' | 'error' | 'warning' | null>(null);
    const isLoading = ref<boolean>(false);

    // Error tracking
    const nameError = ref<boolean>(false);
    const emailError = ref<boolean>(false);
    const messageError = ref<boolean>(false);

    // Validate Name
    const validateName = () => {
      nameError.value = name.value.trim().length === 0;
    };

    // Validate Email
    const validateEmail = () => {
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      emailError.value = !emailRegex.test(email.value);
    };

    // Validate Message
    const validateMessage = () => {
      messageError.value = message.value.trim().length === 0;
    };

    const showNotification = (message: string, type: 'success' | 'error' | 'warning') => {
      notificationMessage.value = message;
      notificationType.value = type;

      // Hide notification after 3 seconds
      setTimeout(() => {
        notificationMessage.value = '';
        notificationType.value = null;
      }, 3000);
    };

    const sendMessage = async () => {
      // Validate inputs before sending
      validateName();
      validateEmail();
      validateMessage();

      if (nameError.value || emailError.value || messageError.value) {
        showNotification('Please correct the errors in the form.', 'warning');
        return;
      }

      isLoading.value = true;

      try {
        await emailjs.send(
          process.env.VUE_APP_EMAILJS_SERVICE_ID,
          process.env.VUE_APP_EMAILJS_TEMPLATE_ID,
          {
            name: name.value,
            email: email.value,
            message: message.value,
          },
          process.env.VUE_APP_EMAILJS_USER_ID
        );

        showNotification('Message sent successfully!', 'success');
        name.value = '';
        email.value = '';
        message.value = '';
      } catch (error) {
        console.log('FAILED...', error);
        showNotification('Failed to send message. Please try again later.', 'error');
      } finally {
        isLoading.value = false;
      }
    };

    return { name, email, message, sendMessage, validateEmail, validateName, validateMessage, notificationMessage, notificationType, isLoading, nameError, emailError, messageError };
  },
});
</script>

<style scoped>
.border-red-500 {
  border-color: #f56565;
}
</style>
