<template>
    <div>
      <div class="fixed top-0 right-0 w-1 h-full bg-gray-200 z-50">
        <div class="w-full bg-green-500 transition-all duration-200" :style="{ height: scrollProgress + '%' }"></div>
      </div>
      <button 
        v-if="showBackToTop" 
        class="fixed bottom-5 right-5 bg-green-500 text-white rounded-full w-10 h-10 flex items-center justify-center shadow-lg transition-opacity duration-200 hover:bg-green-600" 
        @click="scrollToTop"
      >
        
      </button>
    </div>
  </template>
  
  <script lang="ts">
  import { defineComponent, ref, onMounted, onUnmounted } from 'vue';
  
  export default defineComponent({
    name: 'VerticalProgressBar',
    setup() {
      const scrollProgress = ref(0);
      const showBackToTop = ref(false);
  
      const updateScrollProgress = () => {
        const scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        const scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight;
        const clientHeight = document.documentElement.clientHeight || window.innerHeight;
        scrollProgress.value = (scrollTop / (scrollHeight - clientHeight)) * 100;
  
        // Show or hide the Back to Top button
        showBackToTop.value = scrollTop > 100;
      };
  
      const scrollToTop = () => {
        window.scrollTo({ top: 0, behavior: 'smooth' });
      };
  
      onMounted(() => {
        window.addEventListener('scroll', updateScrollProgress);
        updateScrollProgress();
      });
  
      onUnmounted(() => {
        window.removeEventListener('scroll', updateScrollProgress);
      });
  
      return {
        scrollProgress,
        showBackToTop,
        scrollToTop,
      };
    },
  });
  </script>
  
  <style scoped>
  /* No additional styles needed */
  </style>
  