    setup() {
        // declare data prop using ref for reactive
        const lastUpdated = ref('');

        onMounted(() => {
            const date = new Date(document.lastModified);
            // data property has value to be updated
            lastUpdated.value = date.toLocaleDateString('en-US', {
                year: 'numeric',
                month: 'long',
                day: 'numeric'
            });
        })
        // return data prop to use in template
        return { lastUpdated }
    }
