<template>
  <transition name="fade">
    <div v-if="visible" :class="notificationClasses">
      <p class="text-sm sm:text-base">{{ message }}</p>
    </div>
  </transition>
</template>

<script lang="ts">
import { defineComponent, computed, ref, watch, onUnmounted } from 'vue';

export default defineComponent({
  name: 'Notification',
  props: {
    message: {
      type: String,
      required: true,
    },
    type: {
      type: String as () => 'success' | 'error' | 'warning',
      required: true,
      validator: (value: string) => ['success', 'error', 'warning'].includes(value),
    },
  },
  setup(props) {
    const visible = ref(false);
    let hideTimeout: ReturnType<typeof setTimeout>;

    // Watch for changes in the message prop
    watch(() => props.message, (newValue) => {
      if (newValue) {
        visible.value = true;
        clearTimeout(hideTimeout);
        hideTimeout = setTimeout(() => {
          visible.value = false;
        }, 3000); // Adjust duration to 3 seconds
      } else {
        visible.value = false;
      }
    });

    onUnmounted(() => {
      clearTimeout(hideTimeout); // Cleanup timeout on component unmount
    });

    const notificationClasses = computed(() => {
      return {
        'fixed top-4 right-4 p-4 rounded-lg shadow-lg text-white z-50': true,
        'bg-green-500': props.type === 'success',
        'bg-red-500': props.type === 'error',
        'bg-yellow-500': props.type === 'warning',
        'max-w-xs': true, // Set a max-width for the notification
      };
    });

    return { visible, notificationClasses };
  },
});
</script>

<style scoped>
.fade-enter-active, .fade-leave-active {
  transition: opacity 0.5s;
}

.fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */ {
  opacity: 0;
}
</style>
